*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


body {
    font-family: 'montseratt', sans-serif;
}

.app {
    height: 100vh;
    background-image: url('../WeatherIcons//cold-bg.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-in;
}

.app.warm {
    background-image: url('../WeatherIcons//warm-bg.jpg');
}

main {
    padding: 25px;
    min-height: 100vh;
    background-image: linear-gradient( to bottom , rgba( 0 , 0 , 0 , 0.2 ) , rgba( 0 , 0 , 0 , 0.75 ) );
}

.search-box {
    width: 100%;
    margin: 0 0 75px;
}
  
.search-box .search-bar {
    display: block;
    width: 100%;
    padding: 15px;
  
    appearance: none;
    background: none;
    border: none;
    outline: none;
  
    background-color: rgba(255, 255, 255, 0.5);

    font-size: 20px;
    font-weight: 500;
    color: #313131;

    border-radius: 0px 0px 16px 16px;
    margin-top: -25px;

    box-shadow: 5px 5px rgba(0, 0, 0, 0.2);

    transition: 0.2s ease-in;
}

.search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
    color: #FFF;
    font-size: 32px;
    font-weight: 600;

    text-align: center;

    text-shadow: 3px 3px rgba(50 , 50 , 70 , 0.5);
}

.location-box .date {
    color: #FFF;
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(50 , 50 , 70 , 0.5);
}

.weather-box {
    text-align: center;
}

.weather-box .temp {
    position: relative;
    display: inline-block;

    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);

    border-radius: 16px;
    padding: 15px 25px;

    color: #FFF;
    font-size: 102px;
    font-weight: 900;

    text-shadow: 2px 2px rgba(255, 255, 255, 0.2);
    text-align: center;

    box-shadow: 6px 6px rgba( 0 , 0 , 0 , 0.2 );
}

.weather-box .weather {
    color: #FFF;
    font-size: 48px;
    font-weight: 700;

    text-shadow: 3px 3px rgba( 0 , 0 , 0 , 0.2 );
}

.weather-box .more-info {
    margin: 30px;
    color: #FFF;
    font-size: 24px;
    font-weight: 700;

    text-shadow: 2px 2px rgba( 0 , 0 , 0 , 0.2 );
}

.wrong {
    padding-top: 30px;

    text-align: center;
    color: #FFF;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 4px 4px rgba( 0 , 0 , 0 , 0.2 );
}